<template>
  <div id="app">
      <Welcome v-if='!showData' @swipe-band='swipeBand'/>
      <Display v-else/>
  </div>
</template>

<script>
import Display from './components/Display'
import Welcome from './components/Welcome'

export default {
  name: 'app',
  data () {
    return {
        showData: false
    }
  },
  components: {
    Welcome,
    Display
  },
  methods: {
      swipeBand () {
          this.showData = true
      }
  }
}
</script>

